import Container from "../components/Container/Container";

export default function Design() {
  return (
    <Container>
      <div className="page">
        <p>Design</p>
        <style jsx>
          {`
            p {
              font-family: "Raleway";
              margin: 0.625em 0;
            }

            @media only screen and (max-width: 1024px) {
              a {
                padding: 1.5rem 0.5rem;
                margin: 0.125rem 0.25rem;
              }
            }
          `}
        </style>
      </div>
    </Container>
  );
}
